<template>
      <!-- <div class="music-box">
          <span @click="musicHandle">{{ test }}</span>
    </div> -->
  <div class="hello-kv">
    <div class="frames">
      <figure @click="start">
        <img :src="require(`@/assets/images/${keyVisual}.jpg`)" alt="key visual">
        <div
          class="greetings-name text-int"
          ref="greetingName"
        >
          <strong class="tt">{{ groom }}</strong>
          <span class="tt">그리고</span>
          <strong class="tt">{{ bridal }}</strong>
        </div>
      </figure>
    </div>
    <!-- <Fraction
      :fractions="fractions"
    /> -->
  </div>
</template>
<script>
import Fraction from '@/components/Fraction.vue'
import music from '@/assets/images/piano-music.mp3'

export default {
  name: 'Hello',
  components: {
    // Fraction
  },
  props: {
    keyVisual: {
      type: String,
      default: 'img-wed-16'
    },
    groom: String,
    bridal: String,
  },
  data: () => {
    return {
      fractions: [
        {
          shape: 'circle-small',
          color: 'orange',
          top: 7,
          right: 25
        },
        {
          shape: 'circle',
          color: 'brown',
          top: 15,
          right: 6
        },
        {
          shape: 'triangle',
          color: 'red',
          top: 13,
          left: 8
        },
        {
          shape: 'triangle',
          color: 'blue',
          top: 69,
          right: 31
        }
      ],
      musicState: false,
      audio: new Audio(music),
      test: "정지",
      scrollLocation: window.scrollY,
      scrollCheck: "",
      audioState: null,
      interver: "",
    }
  },
  mounted() {
    window.scrollTo(0, 0);
    setTimeout(() => {
      this.$refs.greetingName.classList.add('hello')
    }, 500)
    this.interver = setInterval(() => {
      console.log(this.scrollLocation)
      console.log(window.scrollY)
      if (window.scrollY > 50) {
        console.log("들어옴")
        this.start()
        clearInterval(this.interver)
      }
    }, 1000)
  },
  methods: {
    musicHandle() {
      this.musicState = !this.musicState

      if (this.musicState) {
        this.test = "정지"
        this.audio.play()
          .then((res) => {
            this.audio.loop = true
            this.audio.volume = 0.7
            this.audioState = true
          })
          .catch((err) => {
            this.audioState = false
            this.audio = null
          })
      } else {
        this.test = "재생"
        this.audioState = false
        this.audio.pause()
      }
    },
    start() {
      this.audio.play()
      this.audio.loop = true
      this.audio.volume = 0.7
      this.test = "정지"
    },
  },
}
</script>
<style lang="scss" scoped>
.hello-kv {
  position: relative;
  min-height: 100vh;
  // margin-bottom: #{$top-gap}px;
  color: $col-key;
}

.frames {
  display: block;
  width: 100%;
  width: calc(100% + #{$side-padding * 2});
  margin: 0 -15px 0;

  figure {
    display: block;
    position: relative;
    width: 100%;

    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: #000;
      opacity: 0.3;
    }

    img {
      display: block;
      width: 100%;
    }
  }
}

.greetings-name {
  position: absolute;
  top: 55%;
  left: 0;
  z-index: $z-bg;
  margin-left: $left-gap;
  color: #fff;

  >strong {
    display: block;
    margin-top: 25px;
    font-size: $font-lar;
  }

  >span {
    display: block;
    margin-top: 25px;
    font-size: $font-s;
  }
}

.music-box {
  width: 100%;
  text-align: end;
  z-index: 9999;
}
</style>