<template>
  <div class="container">
    <h4 class="">
      <span class="">5월 13일</span>
    </h4>
    <h6 class="">
      <span class="">토요일 낮 12시</span>
    </h6>
  </div>
  <div class="calender">
    <div class="">
      <div class="">
      <div class="iner-box border-top">
      <div class="day" v-for="(e, i) in dayEn" :key="i">
         {{ e }}
      </div>
    </div>
    <div class="iner-box">
      <div class="day" v-for="(e, i) in first" :key="i">
         {{ e }}
      </div>
    </div>
    <div class="iner-box">
      <div class="day" v-for="(e, i) in second" :key="i">
        <div v-if="e === 7" class="redtxt">{{ e }}</div>
        <div v-if="e === 13" class="Dday">{{ e }}</div>
        <div v-else-if="e !== 7 && e !== 13">{{ e }}</div>
      </div>
    </div>
    <div class="iner-box">
      <div class="day" v-for="(e, i) in third" :key="i">
        <div v-if="e === 14" class="redtxt">{{ e }}</div>
        <div v-else>{{ e }}</div>
      </div>
    </div>
    <div class="iner-box">
      <div class="day" v-for="(e, i) in fourth" :key="i">
        <div v-if="e === 21" class="redtxt">{{ e }}</div>
        <div v-else>{{ e }}</div>
      </div>
    </div>
    <div class="iner-box border-bottom">
      <div class="day" v-for="(e, i) in five" :key="i">
        <div v-if="e === 28" class="redtxt">{{ e }}</div>
        <div v-else>{{ e }}</div>
      </div>
    </div>
    </div>
    </div>
    
  </div>
  <div>
    <div v-if="beforeDay" class="d-day-text">
      <div class="text-int">
        <span class="tt">재은 ❤️ 하리의 결혼식이 <span style="color: #ea7664;">{{ beforeDay }}</span>일 남았습니다.</span>
      </div>
    </div>
    <div v-if="afterDay" class="d-day-text">
      <div class="text-int">
        <span class="tt inline">재은 ❤️ 하리의 결혼식이 <span style="color: #ea7664;">{{ afterDay }}</span>일 지났습니다.</span>
      </div>
    </div>
    <div v-if="toDay" class="d-day-text">
      <div class="text-int">
        <span class="tt inline">재은 ❤️ 하리의 결혼식이 <span style="color: #ea7664;">{{ toDay }}</span> 입니다!
        </span>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Calendar",
  data() {
    return {
      date: 31,
      dayEn: ["S", "M", "T", "W", "T", "F", "S"],
      first: ["", 1, 2, 3, 4, 5, 6],
      second: [7, 8, 9, 10, 11, 12, 13],
      third: [14, 15, 16, 17, 18, 19, 20],
      fourth: [21, 22, 23, 24, 25, 26, 27],
      five: [28, 29, 30, 31, "", "", ""],
      beforeDay: "",
      afterDay: "",
      toDay: "",
    }
  },
  methods: {
    dateCal() {
      var now = new Date();

      var year = now.getFullYear();   // 연도
      var month = now.getMonth() + 1;   // 월    
      var day = now.getDate();        // 일

      var stDate = new Date(2023, 5, 13);
      var endDate = new Date(year, month, day);

      var btMs = endDate.getTime() - stDate.getTime();
      var bfDay = btMs / (1000 * 60 * 60 * 24);
      console.log(bfDay)

      if (bfDay === 0) return this.toDay = "오늘"
      bfDay < 0 ? this.beforeDay = -bfDay : this.afterDay = bfDay
    }
  },
  created() {
    this.dateCal()
  }
}
</script>
<style lang="scss" scoped>
.container {
  position: relative;
  margin-top: 30px;

  h4 {
    text-align: center;
    font-size: $font-xs;
    color: $col-key;
    padding: 10px 0 15px 0;
  }

  h6 {
    text-align: center;
    font-size: $font-xs;
    color: black;
    padding-bottom: 15px;
  }
}

.calender {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.iner-box {
  display: flex;
  justify-content: center;
}

.day {
  width: 65px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: $font-xxs;
}

.Dday {
  border-radius: 50%;
  color: white;
  background-color: $col-brown;
  padding: 8px;
}

.redtxt {
  color: $col-err
}

.border-bottom {
  padding-bottom: 15px;
  border-bottom: 1px solid $col-brown;
}

.border-top {
  padding-top: 15px;
  border-top: 1px solid $col-brown;
}

.d-day-text {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  font-size: $font-xxs;
  padding-top: 20px;
}

.goodbye {
  position: relative;
  padding: #{$top-gap-3x}px 0;

  h3 {
    text-align: center;
    font-size: $font-lar;
    color: $col-key;
  }
}
</style>